* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
  
}

.App {
  background-color: hsl(0, 5%, 87%);
  height: 100%;
  /* overflow-x: hidden; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
img {
  width: 100%;
  height: 50%;
}
.arrange-div {
  flex: 50%;
  width: 40%;
  /* margin: 5%; */
}
.key-div {
  width: 70%;
  margin-left: 12%;
  margin-top: 4%;
  background-color: whitesmoke;
  margin-bottom: 4%;
  padding: 4%;
  box-shadow: 3px 8px 15px 3px #888888;
}
.key-div:hover {
  box-shadow: 3px 8px 15px 6px #888888;
}
.link-title {
  display: flex;
  justify-content: space-around;
  margin: 6%;
}
.link-title a {
  color: aliceblue;
  background-color: rgb(
    58,
    57,
    57
  ); /* For browsers that do not support gradients */
  background-image: linear-gradient(
    180deg,
    rgb(73, 73, 73),
    rgb(223, 223, 219)
  );
  text-decoration: none;
  padding: 3%;
  width: 20%;
  align-items: center;
  align-items: center;
  border-radius: 5%;
  text-align: center;
}
.link-ttle a:active {
  background-image: linear-gradient(
    180deg,
    rgb(223, 223, 219),
    rgb(73, 73, 73)
  );
}
.title-text {
  padding: 3%;
}
.header1 {
  background-color: black;
  display: flex;
  height: 4cm;
  padding: 2%;
  padding-top: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.search-div {
  background-color: white;
  display: flex;
  flex: 40%;
  height: 1%;
  margin-top: 2em;
  /* margin: 1em; */
}
#search {
  padding: 3%;
  width: 8%;
  flex: 85%;
  border: none;
  outline: none;
}
#search input[type="text"]:focus {
  border: 1px solid rgba(240, 248, 255, 0.637);
  /* width: 10cm; */
}
.sign-logo div:nth-of-type(1) {
  flex-grow: 1;
}
.sign-logo div:nth-of-type(2) {
  flex-grow: 1;
}
.sign-logo div:nth-of-type(3) {
  flex-grow: 1.5;
}

.sign-logo {
  /* position: fixed; */
  /* top: 0; */
  width: 100%;
  /* z-index: 1; */
}

.icon-btn {
  /* flex: 10%; */
  display: flex;
  justify-content: center;
  padding-top: 5%;
  background-color: rgba(168, 179, 172, 0.404);
  cursor: pointer;
}
.icon-btn i {
  color: azure;
}
.result-warning {
  text-align: center;
}
@media only screen and (max-width: 915px) {
  .App-header {
    display: grid;
  }
  img {
    width: 100%;
    height: 50%;
  }
  .arrange-div {
    width: 100%;
    /* margin: 5%; */
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
