  width: 48%;
  height: 90%;
  float: left;
}
.sign-logo div {
  flex: 80;
}
.sign-logo .registering-div button {
  flex: 10%;
  height: 40%;
  width: 1%;
  margin-top: 1%;
  margin: 1%;
  background-color: rgb(
    58,
    57,
    57
  ); /* For browsers that do not support gradients */
  background-image: linear-gradient(
    180deg,
    rgb(73, 73, 73),
    rgb(223, 223, 219)
  );
}
.sign-logo .registering-div button:active {
  background-image: linear-gradient(
    180deg,
    rgb(223, 223, 219),
    rgb(73, 73, 73)
  );
}
.registering-div {
  display: flex;
  /* margin: 1.7%; */
  flex: 20%;
}
.project-icons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  justify-content: space-around;
}
.project-icons a{
  /* width: 5.1%; */
  margin-top: 2%;
  border-radius: 7%;
  /* width: 10cm; */
  height: 10cm;

}
:root {
  --clr-neon: hsl(276, 94%, 55%);
  --clr-bg: hsl(220, 13%, 18%);
  --clr-gamer: hsl(128, 96%, 70%);
  --clr-jobs: hsl(251, 98%, 64%);
  --clr-cocktails: rgb(228, 238, 92);
  --clr-publish: rgb(252, 33, 241);
  --clr-video: rgb(223, 165, 126);
  --clr-neon2: hsla(251, 84%, 57%, 0.534);
  --clr-neon3: hsl(251, 97%, 64%);
}


.project-icons a:nth-of-type(1) {
  margin-top: 2em;
  color: var(--clr-neon);
  background-color: var(--clr-bg);
  opacity: 0.9;
  display: inline-block;
  cursor: pointer;
  border: var(--clr-neon) 0.125em solid;
  border-radius: 0.25em;
  padding-top: 1em;
  padding-left: 1.4em;
  padding-right: 1.4em;
  height: 3.2em;
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.5), 0 0 0.45em var(--clr-neon);

  box-shadow: inset 0 0 0.5em 0 var(--clr-neon), 0 0 0.5em 0 var(--clr-neon);
  position: relative;
}
/* .project-icons a:nth-of-type(1)::before {
  pointer-events: none;
  content: "";
  position: absolute;
  background: var(--clr-neon);
  top: 90%;
  left: 0;
  width: 100%;
  height: 100%;

  transform: perspective(2.5em) rotateX(40deg) scale(1, 0.35);
  filter: blur(1em);
} */
.project-icons a:nth-of-type(1)::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: 0 0 2em 0.5em var(--clr-neon);
  opacity: 0;
  transition: opacity 100ms linear;
}
.project-icons a:nth-of-type(1):hover,
.project-icons a:nth-of-type(1):focus {
  background: var(--clr-neon);
  color: var(--clr-bg);
  text-shadow: none;
}
.project-icons a:nth-of-type(1):hover::after,
.project-icons a:nth-of-type(1):focus {
  opacity: 1;
}
.project-icons a:nth-of-type(1):hover::before,
.project-icons a:nth-of-type(1):focus {
  opacity: 1;
}

