.middlelink,
.trendlink {
  position: relative;
  font-family: "Raleway", sans-serif;
  background: var(--clr-neon2);
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1em;
  letter-spacing: 0.0em;
  padding: 7px;
  transition: 0.5s;
  border: 3px solid hsla(251, 84%, 57%, 0.096);
  z-index: 1;
}
.middlelinker,
.trendlinker {
  display: none;
}
.middlelink:hover,
.trendlink:hover {
  background-color: transparent;
  letter-spacing: 0.3em;
  color: var(--clr-neon3);
  box-shadow: 0 0 35px var(--clr-neon3);
  border: 3px solid var(--clr-neon3);
}
.middlelink::before,
.trendlink::before {
  content: "";
  position: absolute;
  inset: 2px;
}
.middlelink span,
.trendlink span {
  position: relative;
  z-index: 1;
}
.middlelink i,
.trendlink i {
  position: absolute;
  inset: 0;
  display: block;
}
.middlelink i::before,
.trendlink i::before {
  content: "";
  position: absolute;
  top: -26%;
  left: 80%;
  width: 10px;
  height: 8px;
  border: 3px solid var(--clr-neon2);
  background: hsl(0, 5%, 87%);
  transition: 0.5s;
  transform: translateX(-50%);
}
.middlelink:hover i::before,
.trendlink:hover i::before {
  width: 20px;
  left: 20%;
  background: var(--clr-neon3);
}
.middlelink i::after,
.trendlink i::after {
  content: "";
  position: absolute;
  bottom: -26%;
  left: 20%;
  width: 10px;
  height: 8px;
  border: 3px solid var(--clr-neon2);
  background: hsl(0, 5%, 87%);
  transition: 0.5s;
  transform: translateX(-50%);
}
.middlelink:hover i::after,
.trendlink:hover i::after {
  width: 20px;
  left: 80%;
  background-color: var(--clr-neon3);
}

